.objects-gallery {

  $block-name:                &; // #{$block-name}__element

  display: flex;
  flex-wrap: wrap;
  margin-bottom: 30px;
  margin-left: -15px;
  margin-right: -15px;
  align-items: flex-start;
  justify-content: flex-start;

  &__item {
    padding-left: 15px;
    padding-right: 15px;
    width: 178px;
    height: 178px;
    overflow: hidden;

    a {
      outline: none;
    }

    img {
      border-radius: 10px;
    }
  }

}
