.docs-gallery {

  $block-name:                &; // #{$block-name}__element

  @include row;

  &__item {
    @include col();
    @include col(lg, 3);
    @include col(md, 4);
    @include col(xs, 6);
    margin-bottom: 17px;
  }
}
